.contain{
    display:grid;
    grid-template-columns: 300px 300px 200px 120px 220px 150px;
    text-align: center;
    padding-left:50px;
}

.oneline{
    display:grid;
    grid-template-columns: 300px 300px 200px 120px 220px ;
    text-align: center;
    margin-top:50px;
    padding-left:70px;
    padding-bottom:13px;
    height:'70px'
}

.oneline h5{
    font-size:17px;
    padding-top:'10px';
}

.contain p, .contain a {
    padding-top:10px;
    font-size: 15px;
}

